<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-close-circle-outline</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>
        <v-list-item v-if="canEditInitialRegistration">
          <v-btn
            class="primary"
            dark
            @click="$router.push('/questionnaire/bhpc-registration/bhpc-registration/' + queries.id + '?practitioner=' + queries.practitioner + '&category=initial')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Edit Registration</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canEditChangeProfession">
          <v-btn
            class="primary"
            dark
            @click="$router.push('/questionnaire/bhpc-registration/bhpc-registration/' + queries.id + '?practitioner=' + queries.practitioner + '&category=profchange')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Edit Registration</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canApproveRegistration">
          <v-btn
            class="primary"
            dark
            @click="$router.push('/questionnaire/bhpc-approve-registration/bhpc-registration-status?practitioner=' + queries.practitioner + '&status=' + latest_status_id + '&category=initial')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Approve Registration</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canApproveChangeProfession">
          <v-btn
            class="primary"
            dark
            @click="$router.push('/questionnaire/bhpc-approve-registration/bhpc-registration-status?practitioner=' + queries.practitioner + '&status=' + latest_status_id + '&category=profchange')"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Approve Prof Change</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canChangeStatus">
          <v-btn
            class="primary"
            dark
            @click="$router.push('/questionnaire/bhpc-change-registration-status/bhpc-registration-status?practitioner=' + queries.practitioner + '&status=' + latest_status_id)"
          >
            <v-icon light left>mdi-check-decagram</v-icon>
            <span>Change Status</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('blue_card')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Blue Card</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('registration_certificate')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Registration Certificate</span>
          </v-btn>
        </v-list-item>
        <v-list-item v-if="canPrintCertificate">
          <v-btn
            @click="print_certificate('letter_good_standing')"
            class="blue"
            dark
          >
            <v-icon light left>mdi-download</v-icon>
            <span>Letter of Good Standing</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-dialog
      v-model="renewDialog"
      persistent
      max-width="500px"
      transition="dialog-transition"
    >
      <v-card>
        <v-toolbar color="primary" class="darken-1 white--text text-uppercase font-weight-bold" dark>
          License Renewal
          <v-spacer></v-spacer>
          <v-btn icon color="white" @click="renewDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <v-menu
            v-model="dateMenu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            min-width="290px"
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="renewalDateFormatted"
                label="Renewal Date*"
                prepend-icon="mdi-event"
                readonly
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="renewalDate"
              @change="renewalDateFormatted = formatDate(renewalDate)"
              @input="dateMenu = false"
            />
          </v-menu>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" :disabled="!renewalDate || disableRenewalBtn" @click="renew">Renew</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-card 
      class="mx-auto"
      max-width="700"
      outlined
      >
      <v-card-actions justify="space-around">
        <v-btn outlined color="indigo" @click="renewDialog = true" :disabled="!canRenewLicense">
          <v-icon left>mdi-autorenew</v-icon>Renew License
        </v-btn>
        <v-btn
          outlined
          color="primary darken-1"
          :disabled="!canChangeProfession"
          :to="'/questionnaire/bhpc-registration/bhpc-registration?practitioner=' + queries.practitioner + '&category=profchange'"
        >
          <v-icon left>mdi-swap-horizontal</v-icon>Change Profession
        </v-btn>
      </v-card-actions>
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        BHPC Registration
      </v-card-title>
      <v-card-text class="my-3">
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Full Name:</span>
          </v-col>
          <v-col cols="8">{{ practitioner.name }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Status:</span>
          </v-col>
          <v-col cols="8">{{ registration.status }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Status Reason:</span>
          </v-col>
          <v-col cols="8">{{ registration.reason }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">BHPC Registration Type:</span>
          </v-col>
          <v-col cols="8">{{ registration.regType }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Profession:</span>
          </v-col>
          <v-col cols="8">{{ registration.prof.type }}, {{ registration.prof.sub }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Application Date:</span>
          </v-col>
          <v-col cols="8">{{ registration.appDate }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Registration Date:</span>
          </v-col>
          <v-col cols="8">{{ registration.regDate }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Submitted Documents:</span>
          </v-col>
          <v-col cols="8">
            <template v-for="doc in registration.docs">
              <div :key="doc.name">{{ doc.name }}</div>
            </template>
          </v-col>
        </v-row>
        <v-divider/>
        <v-row>
          <v-col cols="auto">
            <v-chip color="primary">{{registration.regNumber}}</v-chip>
          </v-col>
          <v-spacer></v-spacer>
          <v-col cols="auto">
            <v-chip color="secondary">{{registration.hpNumber}}</v-chip><br>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-card 
      class="mx-auto"
      max-width="700"
      outlined
      >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        BHPC Current License
      </v-card-title>
      <v-card-text class="my-3">
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Number:</span>
          </v-col>
          <v-col cols="8">{{ license.number }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Start:</span>
          </v-col>
          <v-col cols="8">{{ license.start }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">End:</span>
          </v-col>
          <v-col cols="8">{{ license.end }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Status:</span>
          </v-col>
          <v-col cols="8">
            <v-chip color="error" v-if="license.status == 'Expired'">{{ license.status }}</v-chip>
            <v-chip color="success" v-else>{{ license.status }}</v-chip>
          </v-col>
        </v-row>
        <v-divider/>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      latest_registration: false,
      disableRenewalBtn: false,
      renewDialog: false,
      renewalDate: '',
      dateMenu: false,
      renewalDateFormatted: '',
      overlay: false,
      latest_status_id: '',
      registration: {
        prof: {
          type: '',
          sub: ''
        },
        docs: [],
        status: '',
        statusCode: '',
        reason: '',
        reasonCode: ''
      },
      practitioner: {
        name: ''
      },
      license: {
        number: "",
        start: "",
        end: "",
        lastRenewed: ""
      }
    }
  },
  computed: {
    canEditInitialRegistration() {
      if(
        this.$store.state.user.role === 'bhpc-role-admin-officer' && 
        (this.registration.statusCode === 'pending') &&
        this.registration.reasonCode === 'new_file'
      ) {
        return true
      }
      return false
    },
    canEditChangeProfession() {
      if(
        this.$store.state.user.role === 'bhpc-role-admin-officer' && 
        this.registration.statusCode === 'pending' &&
        this.registration.reasonCode === 'upgrade'
      ) {
        return true
      }
      return false
    },
    canApproveRegistration() {
      if(
        this.$store.state.user.role === 'bhpc-role-registrar' && 
        (this.registration.statusCode === 'pending' || this.registration.statusCode === 'rejected') &&
        this.registration.reasonCode === 'new_file'
      ) {
        return true
      }
      return false
    },
    canApproveChangeProfession() {
      if(
        this.$store.state.user.role === 'bhpc-role-registrar' && 
        (this.registration.statusCode === 'pending' || this.registration.statusCode === 'rejected') &&
        this.registration.reasonCode === 'upgrade'
      ) {
        return true
      }
      return false
    },
    canRenewLicense() {
      if(
        (this.$store.state.user.role === 'bhpc-role-admin-officer' || this.$store.state.user.role === 'bhpc-role-registrar') &&
        (this.registration.statusCode == 'active' || this.registration.statusCode == 'upgrade')
      ) {
        return true
      }
      return false
    },
    canChangeProfession() {
      if(
        (this.$store.state.user.role === 'bhpc-role-admin-officer') &&
        (this.registration.statusCode == 'active' && this.latest_registration)
      ) {
        return true
      }
      return false
    },
    canChangeStatus() {
      if(
        (this.$store.state.user.role === 'bhpc-role-admin-officer' || this.$store.state.user.role === 'bhpc-role-registrar') &&
        this.registration.statusCode !== 'pending' && this.registration.statusCode !== 'rejected'
      ) {
        return true
      }
      return false
    },
    canPrintCertificate() {
      if(
        (this.$store.state.user.role === 'bhpc-role-admin-officer' || this.$store.state.user.role === 'bhpc-role-registrar') &&
        this.registration.statusCode == 'active'
      ) {
        return true
      }
      return false
    }
  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
    print_certificate(type) {
      this.overlay = true
      fetch("/site/bhpc/" + type + "/" + this.queries.id + "/" + this.queries.practitioner).then((response) =>{
        response.blob().then((blob) => {
          fetch("/fhir/Practitioner/" + this.queries.practitioner).then((response) => {
            response.json().then((pract) => {
              this.overlay = false
              let fullname = ""
              let name = pract.name.find((nm) => {
                nm.use === "official"
              })
              if(!name && pract.name && pract.name.length > 0) {
                name = pract.name[0]
              }
              if(name) {
                let prefix = name.extension && name.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-prefix"
                })
                if(prefix) {
                  fullname += prefix.valueCoding.display
                }
                if(name.given) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.given.join(" ")
                }
                if(name.family) {
                  if(fullname) {
                    fullname += " "
                  }
                  fullname += name.family
                }
              }
              let link = document.createElement("a");
              link.href = window.URL.createObjectURL(blob);
              link.download = `${fullname}.pdf`;
              link.click();
            }).catch(() => {
              this.overlay = false
            })
          }).catch(() => {
              this.overlay = false
            })
        }).catch(() => {
          this.overlay = false
        })
      }).catch(() => {
        this.overlay = false
      })
    },
    renew() {
      this.overlay = true;
      this.disableRenewalBtn = true
      this.renewDialog = false
      let body = {
        reg_status_id: this.latest_status_id,
        practitioner: this.queries.practitioner,
        reg_id: this.queries.id,
        license_number: this.registration.regNumber,
        date: this.renewalDate
      }
      fetch("/site/bhpc/renewLicense", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        redirect: "manual",
        body: JSON.stringify(body),
      }).then((response) => {
        this.overlay = false;
        this.disableRenewalBtn = false
        if(response.status === 201) {
          this.getRegistration()
          this.getPractitioner()
          this.getLatestLicense()
          this.getLatestStatus()
          this.$store.commit("setMessage", {
            type: "success",
            text: "License Renewed",
          });
        } else {
          this.$store.commit("setMessage", {
            type: "error",
            text: "Failed to renew license."
          });
        }
      }).catch(() => {
        this.overlay = false;
        this.disableRenewalBtn = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "Failed to renew license."
        });
      })
    },
    getRegistration() {
      fetch("/fhir/Basic/" + this.queries.id).then((response) => {
        response.json().then(async(results) => {
          let regType = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-registration-type"
          })
          if(regType) {
            this.registration.regType = regType.valueCoding.display
            if(!this.registration.regType) {
              this.registration.regType = await this.$fhirutils.codeLookup(regType.valueCoding.system, regType.valueCoding.code)
            }
          }
          let prof = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bw-profession-subtype"
          })
          if(prof) {
            fetch("/fhir/Basic/" + prof.valueReference.reference.split("/")[1]).then((profResponse) => {
              profResponse.json().then(async(profResults) => {
                let profSub = profResults.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-basic-name"
                })
                if(profSub) {
                  this.registration.prof.sub = profSub.valueString
                }
                let profType = profResults.extension.find((ext) => {
                  return ext.url === "http://ihris.org/fhir/StructureDefinition/bw-profession-type"
                })
                this.registration.prof.type = profType.valueCoding.display
                if(!this.registration.prof.type) {
                  this.registration.prof.type = await this.$fhirutils.codeLookup(profType.valueCoding.system, profType.valueCoding.code)
                }
              })
            })
          }
          let appDate = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/application-date"
          })
          if(appDate) {
            let dt = appDate.valueDate.split("-")
            dt = dt[2]+"-"+dt[1]+"-"+dt[0]
            this.registration.appDate = dt
          }
          let regDate = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/registration-date"
          })
          if(regDate) {
            let dt = regDate.valueDate.split("-")
            dt = dt[2]+"-"+dt[1]+"-"+dt[0]
            this.registration.regDate = dt
          }
          let regNumber = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-registration-number"
          })
          if(regNumber) {
            this.registration.regNumber = regNumber.valueString
          }
          let hpNumber = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-hp-number"
          })
          if(hpNumber) {
            this.registration.hpNumber = hpNumber.valueString
          }
          let docs = results.extension.filter((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-submitted-document"
          })
          this.registration.docs = []
          for(let doc of docs) {
            let name = doc.extension.find((ext) => {
              return ext.url === "name"
            })
            if(name) {
              let display = name.valueCoding.display
              if(!display) {
                display = await this.$fhirutils.codeLookup(name.valueCoding.system, name.valueCoding.code)
              }
              this.registration.docs.push({
                name: name.valueCoding.code + ": " + display
              })
            }
          }
        })
      })
    },
    getPractitioner() {
      fetch("/fhir/Practitioner/" + this.queries.practitioner + "?_elements=name").then((response) => {
        response.json().then((results) => {
          this.practitioner.name = results.name[results.name.length-1].given.join(', ') + ' ' + results.name[results.name.length-1].family
        })
      })
    },
    getLatestLicense() {
      let queries = "?_profile=http://ihris.org/fhir/StructureDefinition/bhpc-license-profile&bhpcregistration=Basic/"+this.queries.id+"&_sort=-_id&_count=1"
      fetch("/fhir/Basic" + queries).then((response) => {
        response.json().then((results) => {
          let resource = results.entry[0].resource
          let start = resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/start-date"
          }).valueDate
          start = start.split("-")
          this.license.start = start[2]+"-"+start[1]+"-"+start[0]
          let end = resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/end-date"
          }).valueDate
          if(end > new Date().toJSON().slice(0, 10)) {
            this.license.status = "Active"
          } else {
            this.license.status = "Expired"
          }
          end = end.split("-")
          this.license.end = end[2]+"-"+end[1]+"-"+end[0]
          this.license.number = resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/license-number"
          }).valueString
        })
      })
    },
    async getLatestRegistration() {
      let queries = "?_profile=http://ihris.org/fhir/StructureDefinition/bhpc-registration-profile&practitioner=Practitioner/" + this.queries.practitioner + "&_sort=-_id&_count=1"
      fetch("/fhir/Basic" + queries).then((response) => {
        response.json().then((results) => {
          if(results.entry[0].resource.id === this.queries.id) {
            this.latest_registration = true
          }
        })
      })
    },
    async getLatestStatus() {
      let queries = "?_profile=http://ihris.org/fhir/StructureDefinition/bhpc-registration-status-profile&bhpcregistration=Basic/" + this.queries.id + "&practitioner=Practitioner/" + this.queries.practitioner + "&_sort=-_id&_count=1"
      fetch("/fhir/Basic" + queries).then((response) => {
        response.json().then(async(results) => {
          let resource = results.entry[0].resource
          this.latest_status_id = resource.id
          let statusCoding = resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-file-status"
          }).valueCoding
          this.registration.statusCode = statusCoding.code
          this.registration.status = statusCoding.display
          if(!this.registration.status) {
            this.registration.status = await this.$fhirutils.codeLookup(statusCoding.system, statusCoding.code)
          }
          let reasonCoding = resource.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-file-status-reason"
          }).valueCoding
          this.registration.reason = reasonCoding.display
          this.registration.reasonCode = reasonCoding.code
          if(!this.registration.reason) {
            this.registration.reason = await this.$fhirutils.codeLookup(reasonCoding.system, reasonCoding.code)
          }
        })
      })
    }
  },
  created() {
    this.getRegistration()
    this.getLatestRegistration()
    this.getPractitioner()
    this.getLatestLicense()
    this.getLatestStatus()
  }
}
</script>